<template>
    <Layout class="wrapper">
        <Header class="header">
            <Menu mode="horizontal" theme="dark" active-name="1">
                <div class="layout-logo"></div>
                <div class="layout-nav">
                    <MenuItem name="1">
                        <Icon type="ios-navigate"></Icon>
                        Item 1
                    </MenuItem>
                </div>
            </Menu>
        </Header>
        <Layout class="content">
            <Sider class="sider" hide-trigger :width="400" style="background-color: white">
                <Menu style="width: 100%" @on-select="onSelectChange">
                    <Submenu :name="doc.title" v-for="doc of docs">
                        <template slot="title">
                            <Icon type="ios-filing"/>
                            {{doc.title}}
                        </template>
                        <template v-for="item of doc.children">
                            <MenuItem v-if="!item.children" :name="item.sha">{{item.title}}
                            </MenuItem>
                            <Submenu :name="item.title" v-else>
                                <template slot="title">{{item.title}}</template>
                                <MenuItem v-for="sub of item.children" :name="sub.sha">
                                    {{sub.title}}
                                </MenuItem>
                            </Submenu>
                        </template>
                    </Submenu>
                </Menu>
            </Sider>
            <Layout :style="{padding: '0 24px 24px'}" style="background-color: white">
                <Content :style="{height: '90%'}">
                    <Row style="height:100%">
                        <Col :span="20" style="height: 100%;padding: 24px 48px">
                            <div v-if="html" v-html="html" class="markdown-body"
                                 style="height:100%;max-width: 752px;margin: 24px auto"></div>
                        </Col>
                        <Col :span="4" style="padding-left:2.5rem;">
                            <Anchor style="position: fixed" show-ink>
                                <AnchorLink href="#basic_usage" title="Basic Usage"/>
                                <AnchorLink href="#static_position" title="Static Position"/>
                                <AnchorLink href="#API" title="API">
                                    <AnchorLink href="#Anchor_props" title="Anchor props"/>
                                    <AnchorLink href="#Anchor_events" title="Anchor events"/>
                                    <AnchorLink href="#AnchorLink_props" title="AnchorLink props"/>
                                </AnchorLink>
                            </Anchor>
                        </Col>
                    </Row>
                </Content>
            </Layout>
        </Layout>
    </Layout>
</template>

<script>
    import {mapState} from 'vuex';
    import marked from 'marked';
    import highlight from 'highlight.js';

    marked.setOptions({
        renderer: new marked.Renderer(),
        highlight: function (code) {
            return highlight.highlightAuto(code).value;
        },
        pedantic: false,
        gfm: true,
        tables: true,
        breaks: false,
        sanitize: false,
        smartLists: true,
        smartypants: false,
        xhtml: false
    });

    export default {
        name: "app",
        created: function () {
            this.repos.forEach(repo => {
                this.$store.dispatch("getTree", repo).then();
            })
        },
        computed: {
            ...mapState(["repos", 'docs', 'markdownMap'])
        },
        methods: {
            onSelectChange: function (sha) {
                const markdown = this.markdownMap.get(sha);
                const repo = markdown.repo;
                this.$store.dispatch('getBlobs', {repo, sha})
                    .then(res => this.html = marked(res));
            }
        },
        data: function () {
            return {
                html: null
            }
        }
    };
</script>

<style>
    @import "assets/github-markdown.css";
    @import "../node_modules/highlight.js/styles/atelier-estuary-light.css";

    html,
    body {
        height: 100%;
    }

    .wrapper {
        height: 100%;
    }

    .sider {
        position: fixed;
        height: 100%;
        left: 0;
        overflow: auto;
        background-color: white;
    }

    .content {
        height: calc(100% - 64px);
    }

</style>
